import logo from './logo.svg';
import './App.css';
import react from 'react';
import AboutMe from './components/Aboutme';
import "@fortawesome/fontawesome-free/css/all.min.css";
import "bootstrap-css-only/css/bootstrap.min.css";
import "mdbreact/dist/css/mdb.css";
import { MDBBtn, MDBCol, MDBContainer, MDBIcon, MDBNav, MDBNavItem, MDBNavLink, MDBRow, MDBTabContent, MDBTabPane } from 'mdbreact';
import { BrowserRouter as Router, Route } from "react-router-dom";
import Projects from './components/Projects';
import Background from "./images/background.png";
import Backgrounddark from "./images/Darkenbackground.jpg";



//var Background = "./images/background.png";

class App extends react.Component {
  state = {
    items: {
      default: "1",
    },
    loading:true,
    darkmode:false
  };
  componentDidMount(){
    this.setState({loading:false})
  }

  changetoproject=()=>{
    this.state.items["default"]="2";
    this.setState({ loading: false }) 
  }

  toggledarkmode=()=>{
    this.setState({darkmode:(!this.state.darkmode)})
  }

  togglePills = (type, tab) => (e) => {
    e.preventDefault();
    if (this.state.items[type] !== tab) {
      let items = { ...this.state.items };
      items[type] = tab;
      this.setState({
        items,
      });
    }
  };

  render() {
    if(this.state.loading) return null;
    console.log(this.state.darkmode);
    return (
      <div>
        <Router>
          <MDBRow
            style={{
              backgroundColor: this.state.darkmode ? "#4d4d4d" : "#c1ccd4",
              backgroundImage: this.state.darkmode
                ? `url(${Backgrounddark})`
                : `url(${Background})`,
              backgroundPosition: "center",
              backgroundRepeat: "no-repeat",
              backgroundSize: "cover",
            }}
          >
            <MDBCol md="12" style={{ paddingleft: "100px" }}>
              <MDBNav className="mt-5 nav-pills">
                <MDBNavItem>
                  <MDBNavLink
                    link
                    to="#"
                    active={this.state.items["default"] === "1"}
                    onClick={this.togglePills("default", "1")}
                  >
                    <a
                      style={{
                        paddingRight: "15px",
                        paddingLeft: "15px",
                        fontFamily: "'Cedarville Cursive', cursive",
                      }}
                    >
                      Cht &nbsp;
                    </a>
                    |&nbsp;&nbsp;&nbsp;
                    <MDBIcon far icon="user-circle" /> &nbsp; Profile
                  </MDBNavLink>
                </MDBNavItem>
                <MDBNavItem>
                  <MDBNavLink
                    link
                    to="#"
                    active={this.state.items["default"] === "2"}
                    onClick={this.togglePills("default", "2")}
                  >
                    <MDBIcon icon="laptop-code" /> &nbsp; Projects
                  </MDBNavLink>
                </MDBNavItem>
                <MDBNavItem style={{ textAlign: "right", paddingTop: "8px" }}>
                  <div className="custom-control custom-switch">
                    &nbsp; &nbsp; &nbsp;
                    <input
                      type="checkbox"
                      className="custom-control-input"
                      id="customSwitchesChecked"
                      checked={this.state.darkmode}
                      onClick={this.toggledarkmode}
                    />
                    <label
                      className="custom-control-label"
                      htmlFor="customSwitchesChecked"
                      style={{
                        color: this.state.darkmode ? "#ffffff" : "black",
                      }}
                    >
                      Dark Mode
                    </label>
                  </div>
                </MDBNavItem>
              </MDBNav>
              <MDBTabContent activeItem={this.state.items["default"]}>
                <MDBTabPane tabId="1">
                  &nbsp;
                  <p>
                    <AboutMe
                      changeproject={this.changetoproject}
                      darkmode={this.state.darkmode}
                    ></AboutMe>
                  </p>
                </MDBTabPane>
                <MDBTabPane tabId="2">
                  <p>
                    <Projects darkmode={this.state.darkmode}></Projects>
                  </p>
                </MDBTabPane>
              </MDBTabContent>
            </MDBCol>
          </MDBRow>
          {() => {
            console.log("tester");
            if (this.state.items["default"] == 2) {
              console.log("tab should be 2");
            }
          }}
        </Router>
      </div>
    );
  }
}

export default App;
